}

input[type="submit"].fit,
input[type="reset"].fit,
input[type="button"].fit,
button.fit,
.button.fit {
  width: 100%;
}

input[type="submit"].small,
input[type="reset"].small,
input[type="button"].small,
button.small,
.button.small {
  font-size: 0.8em;
}

input[type="submit"].large,
input[type="reset"].large,
input[type="button"].large,
button.large,
.button.large {
  font-size: 1.35em;
}

input[type="submit"].disabled,
input[type="submit"]:disabled,
input[type="reset"].disabled,
input[type="reset"]:disabled,
input[type="button"].disabled,
input[type="button"]:disabled,
button.disabled,
button:disabled,
.button.disabled,
.button:disabled {
  pointer-events: none;
  opacity: 0.5;
}

/* Main */

#main {
  -moz-transform-origin: 50% 50%;
  -webkit-transform-origin: 50% 50%;
  -ms-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
  -moz-transform: rotateX(0deg);
  -webkit-transform: rotateX(0deg);
  -ms-transform: rotateX(0deg);
  transform: rotateX(0deg);
  -moz-transition: opacity 1s ease, -moz-transform 1s ease;
  -webkit-transition: opacity 1s ease, -webkit-transform 1s ease;
  -ms-transition: opacity 1s ease, -ms-transform 1s ease;
  transition: opacity 1s ease, transform 1s ease;
  padding: 4.5em 3em 3em 3em;
  background: #ffffff;
  border-radius: 4px;
  cursor: default;
  max-width: 100%;
  opacity: 0.95;
  position: relative;
  text-align: center;
  width: 27em;
  margin-bottom: 50px;
}

#main .avatar {
  position: relative;
  display: block;
  margin-bottom: 1.5em;
}

#main .avatar img {
  display: block;
  margin: 0 auto;
  border-radius: 100%;
  box-shadow: 0 0 0 1.5em #ffffff;
}

#main .avatar:before {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: -3em;
  width: calc(100% + 6em);
  height: 1px;
  z-index: -1;
  background: #c8cccf;
}

@media screen and (max-width: 480px) {
  #main {
    padding: 4em 2em 2.5em 2em;
    width: 100%;
  }

  #main .avatar:before {
    left: -2em;
    width: calc(100% + 4em);
  }
}

body.is-preload #main {
  -moz-transform: rotateX(15deg);
  -webkit-transform: rotateX(15deg);
  -ms-transform: rotateX(15deg);
  transform: rotateX(15deg);
  opacity: 0;
}

/* Footer */

#footer {
  -moz-align-self: -moz-flex-end;
  -webkit-align-self: -webkit-flex-end;
  -ms-align-self: -ms-flex-end;
  align-self: flex-end;
  width: 100%;
  padding: 1.5em 0 0 0;
  color: rgba(255, 255, 255, 0.75);
  cursor: default;
  text-align: center;
}

#footer .copyright {
  margin: 0;
  padding: 0;
  font-size: 0.9em;
  list-style: none;
}

#footer .copyright li {
  display: inline-block;
  margin: 0 0 0 0.45em;
  padding: 0 0 0 0.85em;
  border-left: solid 1px rgba(255, 255, 255, 0.5);
  line-height: 1;
}

#footer .copyright li:first-child {
  border-left: 0;
}
/* Footer */
.pdf {
  margin: auto;
  padding: 0;
  border: 0;
  display: block;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  text-align: center;
  margin: 0;
  position: relative;
  overflow: auto;
}
.pdf iframe {
  margin: 0;
  padding: 0;
  position: relative;
  width: 100%;
  height: calc(100vh - 16px);
}

.slides {
  margin: auto;
  padding: 0;
  border: 0;
  display: block;
  font-size: 100%;
  height: 100%;
  font: inherit;
  vertical-align: baseline;
  text-align: center;
  margin: 0;
  position: relative;
  overflow: auto;
}
/* Wrapper */

#wrapper {
  display: -moz-flex;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  -moz-align-items: center;
  -webkit-align-items: center;
  -ms-align-items: center;
  align-items: center;

  -moz-justify-content: stretch;
  -webkit-justify-content: stretch;
  -ms-justify-content: stretch;
  justify-content: stretch;
  /*
		-moz-justify-content: space-between;
		-webkit-justify-content: space-between;
		-ms-justify-content: space-between;
		justify-content: space-between;
    */
  -moz-flex-direction: column;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  -moz-perspective: 1000px;
  -webkit-perspective: 1000px;
  -ms-perspective: 1000px;
  perspective: 1000px;
  position: relative;
  height: auto;
  margin: auto;
  min-height: 100%;
  padding: 0;
  z-index: 2;
}

#wrapper > * {
  z-index: 1;
}

#wrapper:before {
  content: "";
  display: block;
}

@media screen and (max-width: 800px) {
  .longtext {
    margin: 10px;
    width: 100%;
  }
  .positionheader {
    flex-direction: column;
  }
  #wrapper {
    padding: 0.75em;
  }
  #wrapper .pdf {
    margin: auto;
    padding: 2%;
    position: relative;
    width: 100%;
    /* min-height: 100%; */
    flex-direction: column;
    flex: 1 1 0;
  }

  #wrapper .slides {
    margin: auto;
    padding: 2%;
    position: relative;
    width: 90%;
    /* min-height: 100%; */
    flex-direction: column;
    flex: 1 1 0;
  }
}

body.is-ie #wrapper {
  height: 100%;
}
@media screen and (min-width: 800px) {
  #wrapper .pdf {
    display: flex;
    margin: auto;
    padding: 2%;
    position: relative;
    width: 60%;
    /* height: 100%; */
    /* padding-bottom: 60%; */
    min-height: 100vh;
    flex-direction: column;

    flex: 1 1 0;
  }

  #wrapper .slides {
    margin: auto;
    padding: 2%;
    position: relative;
    width: 60%;
    /* min-height: 100vh; */
    flex-direction: column;
    flex: 1 1 0;
  }
}
